import uniIcon from '@/components/uni-icon/uni-icon.vue';
import uniIconfont from '@/components/uni-iconfont/uni-icon.vue'
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue'
import {turnPage} from '@/common/global.js';
import {dataFormat} from '@/common/tool.js';
import {ajaxGetOrders, ajaxDelOrder} from '@/data/ajax.js';

export default {
	components: {
		uniIcon,
		uniIconfont,
		uniLoadMore
	},
	data() {
		return {
			orders: [],
			// 分页-当前页
			page: 1,
			// 分页-每页大小
			size: 10,
			// 分页-总页数
			totalPage: 1,
			// 上拉显示更多组件
			// more loading noMore
			status: 'more',
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '没有更多'
			}
		};
	},
	computed: {},
	onLoad(e) {
		let _this = this;
		// 获取订单数据
		this.getOrderList(() => {
			_this.status = _this.totalPage > _this.page ? 'more' : 'noMore';
		});
	},
	onPullDownRefresh () {
		let _this = this;
		_this.page = 1;
		_this.status = 'loading';
		_this.getOrderList(() => {
			uni.stopPullDownRefresh();
			_this.status = _this.totalPage > _this.page ? 'more' : 'noMore';
		});
	},
	onReachBottom () {
		let _this = this;
		if (_this.totalPage > _this.page) {
			_this.page += 1;
			// 获取列表数据
			_this.status = 'loading';
			_this.getOrderList(() => {
				_this.status = 'more';
			});
		} else {
			// 没有更多数据
			_this.status = 'noMore';
		}
	},
	methods: {
		clkOrder (data) {
			turnPage('order-info', data.id);
		},
		clkDel (index) {
			console.log(index);
			let _this = this;
			let data = _this.orders[index];
			uni.showModal({
				title: '取消订单',
				content: '确定取消该订单？',
				success: (res) => {
					if (res.confirm) {
						ajaxDelOrder(data, (result) => {
							uni.showToast({title: '订单已取消！'});
							_this.orders.splice(index, 1);
						});
					}
				}
			});
		},
		getOrderList (callback) {
			let _this = this;
			ajaxGetOrders({
				page: _this.page,
				size: _this.size
			}, (data) => {
				// 计算总页数
				_this.totalPage = parseInt((data.result.total - 1) / _this.size + 1);
				data = data.result.list || [];
				if (_this.page <= 1) {
					_this.orders = data;
				} else {
					_this.orders = _this.orders.concat(data);
				}
				callback && callback();
			});
		},
		formateDate (date) {
			return dataFormat(new Date(date), 'yyyy-MM-dd hh:mm');
		}
	}
};
